html {
  height: 100%;
}

.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 10px;

  @media (min-width: 768px) {
    width: 768px;
    padding: 0 20px;
  }

  @media (min-width: 1200px) {
    width: 1200px;
  }
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.header {
  flex: 0 0 auto;
}

.main {
  flex: 1 1 auto;
}

.footer {
  flex: 0 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
